body {
  font-family:sans-serif;
  font-size: 1.2rem;
}

#root {
  margin: 50px;
}

#root input {
  display: block;
  border-color: #505050;
  border-radius: 5px;
  margin: 5px 0 20px 0;
}

#root input:hover{
  border-color: #000000;
}

#root ul {
  padding: 10px 0;
}

#root ul li {
  list-style-type: none;
  padding: 2px 0;

}